@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@-webkit-keyframes moveup{from{transform:translateY(0)}to{transform:translateY(-50px)}}@keyframes moveup{from{transform:translateY(0)}to{transform:translateY(-50px)}}.miniContainer{-webkit-animation-name:moveup;animation-name:moveup;-webkit-animation-duration:0.5s;animation-duration:0.5s;transform:translateY(-50px)}.container{width:50%}.container .searchAreaContainer{display:flex;flex-direction:column;height:120px;justify-content:space-between}.container .searchAreaContainer .title{font-size:30px;color:#949405;background-color:#f5cf29;width:-webkit-max-content;width:-moz-max-content;width:max-content;margin:0 auto;padding:10px;font-weight:700;box-shadow:0px 0px 10px #cca70a}.container .searchAreaContainer .searchHolder{position:relative}.container .searchAreaContainer .searchHolder:focus-within{box-shadow:0px 0px 10px #f5cf29}.container .searchAreaContainer .searchHolder .searchBar{border:2px solid #f5cf29}.container .searchAreaContainer .searchHolder .searchBar input{font-family:'Montserrat', sans-serif;width:100%;background-color:transparent;border:none;margin:0;padding:2px;color:rgba(0,0,0,0.87);word-wrap:break-word;outline:none;display:flex;flex:100% 1;-webkit-tap-highlight-color:transparent;margin-top:-37px;height:34px;font-size:16px}.container .searchAreaContainer .searchHolder .searchBar .suggestionContainer{position:absolute;top:10px}.container .searchAreaContainer .searchHolder .searchBar .suggestionContainer .eachSuggetion{background-color:white;padding:10px;cursor:pointer}.container .miniSearchArea{flex-direction:row;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;justify-content:center;align-items:center;margin-bottom:20px}.container .miniSearchArea .title{letter-spacing:3px;box-shadow:none;border-radius:5px;flex:1 1;font-size:13px;text-align:center;margin-right:10px;padding:10px 3px}.container .miniSearchArea .searchHolder{flex:4 1;transform:translateY(15px)}

.donationContainer .form{display:flex;flex-direction:row}

@-webkit-keyframes shake{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, 0, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0)}40%,60%{transform:translate3d(4px, 0, 0)}}@keyframes shake{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, 0, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0)}40%,60%{transform:translate3d(4px, 0, 0)}}.donationFormContainer{flex:3 1;font-family:'Montserrat', sans-serif}.donationFormContainer .donationForm{display:flex;flex-direction:column;padding:10px 20px;margin-top:30px}.donationFormContainer .donationForm .formInput{margin-bottom:60px;border-bottom:2px solid #f5cf29}.donationFormContainer .donationForm .formInput input{width:100%;color:#949405;background-color:transparent;border:none;margin:0;padding:2px;word-wrap:break-word;outline:none;display:flex;flex:100% 1;-webkit-tap-highlight-color:transparent;margin-top:-37px;height:34px;font-size:16px}.donationFormContainer .donationForm .error{border-bottom:2px solid #ff4e08;-webkit-animation:shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;animation:shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;transform:translate3d(0, 0, 0)}.donationFormContainer .donationForm .action button{border:none;color:#949405;background-color:#f5cf29;width:100%;height:50px;border-radius:9px;box-shadow:0px 0px 3px #cca70a}

.targetContainer{flex:2 1;display:flex;flex-direction:column;justify-content:space-around}.targetContainer .part{flex:1 1}.targetContainer .part .label{font-weight:bold;font-size:20px;margin:10px 0;width:100%}.targetContainer .part .amount{width:100%;margin:10px 0;font-size:20px;color:#949405}

.donerList{margin-top:30px}.donerList .label{text-align:center;font-size:30px;font-weight:bold;border-bottom:2px solid #cca70a;border-top:2px solid #cca70a;padding:10px 0}.donerList .list{margin-top:20px}.donerList .list .doner{display:flex;justify-content:space-between;padding:10px 20px;margin:8px 0}.donerList .list .doner:nth-child(2n){background-color:#f9f1ce}.donerList .list .doner .name{font-size:23px}.donerList .list .doner .amount{font-weight:bold;font-size:27px}

.app{display:flex;padding:10px;height:100vh;justify-content:center;align-items:center;font-family:'Montserrat', sans-serif}

